let { toast } = require("react-toastify");

const notify = (type, message) => {
  switch (type) {
    case "info":
      toast.info(message);
      break;
    case "success":
      toast.success(message);
      break;
    case "warning":
      toast.warn(message);
      break;
    case "error":
      toast.error(message);
      break;
    case "dark":
      toast.dark(message);
      break;
    default:
      toast(message);
  }
};

module.exports = notify;
