const notify = require("./notify");
const axios = require("axios").default;
let EDU_AXIOS;
let token = "";
// to set authorization header on test mode
let user = localStorage.getItem("user");
if (user) {
  token = JSON.parse(localStorage.getItem("user")).token;
}
if (process.env.REACT_APP_APP_ENV == "production") {
  EDU_AXIOS = axios.create({
    timeout: 6000,
    withCredentials: true,
    credentials: "include",
    headers: {
      "Content-Type": "application/json",
    },
  });
  /**
   * Catch the AunAuthorized Request
   */
  EDU_AXIOS.interceptors.response.use(
    (response) => response,
    (error) => {
      if (error.response.status === 401) {
        localStorage.removeItem("user");
        window.location = "/#/login";
        notify("error", error.response.message);
      } else {
        notify("error", error.response.message);
      }
    }
  );
}
if (process.env.REACT_APP_APP_ENV == "test") {
  if (token == "") {
    console.log("there isno token");
    EDU_AXIOS = axios.create({
      timeout: 6000,
      headers: {
        "Content-Type": "application/json",
      },
    });
  } else {
    EDU_AXIOS = axios.create({
      timeout: 6000,
      headers: {
        "Content-Type": "application/json",
        authorization: "Bearer " + token,
      },
    });
  }

  /**
   * Catch the AunAuthorized Request
   */
  EDU_AXIOS.interceptors.response.use(
    (response) => response,
    (error) => {
      if (error.response.status === 401) {
        localStorage.removeItem("user");
        window.location = "/#/login";
        console.log(error.response.data.message);
        notify("error", error.response.data.message);
      } else {
        notify("error", error.response.data.message);
      }
    }
  );
}

module.exports = EDU_AXIOS;
