import React from "react";
import { CFormCheck } from "@coreui/react-pro";
import { connect, Field, ErrorMessage, getIn } from "formik";
import TextError from "./TextError";
const CheckBox = (props) => {
  const { formik, name, options, switcher, ...rest } = props;
  const error = getIn(formik.errors, name);
  const touch = getIn(formik.touched, name);
  return (
    <>
      <Field name={name} {...rest}>
        {({ field }) => {
          return options.map((option) => {
            return (
              <React.Fragment key={option.text}>
                <CFormCheck
                  id={option.value}
                  {...field}
                  {...rest}
                  value={option.value}
                  checked={
                    field.value ? field.value.includes(option.value) : ""
                  }
                  label={option.text}
                  disabled={option.disabled ? option.disabled : false}
                  className={`${error && touch ? "is-invalid" : ""}
                  }`}
                />
              </React.Fragment>
            );
          });
        }}
      </Field>
      <ErrorMessage name={name} component={TextError} />
    </>
  );
};

export default connect(CheckBox);
