import React from "react";
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CInputGroup,
  CRow,
  CFormFloating,
  CFormLabel,
} from "@coreui/react-pro";
import { useHistory } from "react-router-dom";
import EDU_AXIOS from "../../../helpers/edu_axios";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import EVFormikControl from "src/ev_formik_controls/EVFormikControl";
import Loader from "src/loader/Loader";

const Login = (props) => {
  // formik params
  const initialValues = {
    username: "",
    password: "",
  };
  const validationSchema = Yup.object({
    username: Yup.string()
      .min(3, "Must have atleast 3 characters")
      .max(15, "Must be 15 characters or less")
      .required("Username is required"),
    password: Yup.string()
      .min(6, "Must have atleast 6 characters")
      .max(15, "Must be 15 characters or less")
      .required("Password is required"),
  });
  let history = useHistory();

  const login = async (username, password) => {
    return await EDU_AXIOS.post(
      process.env.REACT_APP_API_PATH + "/auth/login",
      {
        username,
        password,
      }
    )
      .then(async (res) => {
        console.log(res);
        localStorage.setItem("user", res.data.data);
        // if app in development phase
        if (process.env.REACT_APP_APP_ENV == "test") {
          let token = JSON.parse(localStorage.getItem("user")).token;
          EDU_AXIOS.interceptors.request.use(
            (config) => {
              if (token) {
                config.headers["authorization"] = "Bearer " + token;
              }
              // config.headers['Content-Type'] = 'application/json';
              return config;
            },
            (error) => {
              Promise.reject(error);
            }
          );
        }
        history.push("/dashboard");
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const onSubmit = async (values) => {
    await login(values.username, values.password);
  };

  return (
    <div className="bg-light min-vh-100 d-flex flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
          <CCol md="5">
            <CCardGroup>
              <CCard className="p-4">
                <CCardBody>
                  <h4>Login</h4>
                  <p className="text-medium-emphasis">
                    Sign In to your Account
                  </p>
                  <Formik
                    initialValues={initialValues}
                    validationSchema={validationSchema}
                    onSubmit={onSubmit}
                  >
                    {(formik) => (
                      <Form>
                        <CFormFloating className="mb-3">
                          <EVFormikControl
                            control="input"
                            type="text"
                            placeholder="Username"
                            name="username"
                            id="username"
                          />
                          <CFormLabel
                            htmlFor="username"
                            className="required-field"
                          >
                            Username
                          </CFormLabel>
                        </CFormFloating>
                        <CFormFloating className="mb-3">
                          <EVFormikControl
                            control="input"
                            type="password"
                            placeholder="Password"
                            name="password"
                            id="password"
                          />
                          <CFormLabel
                            htmlFor="password"
                            className="required-field"
                          >
                            Password
                          </CFormLabel>
                        </CFormFloating>
                        <CRow>
                          <CCol xs="6">
                            <CButton
                              type="submit"
                              color="dark"
                              className="px-4"
                              disabled={
                                formik.isSubmitting ||
                                !formik.dirty ||
                                !formik.isValid
                              }
                            >
                              {formik.isSubmitting ? <Loader /> : "Login"}
                            </CButton>
                          </CCol>
                          <CCol xs="6" className="text-right">
                            <CButton color="gray" className="px-0">
                              Forgot password?
                            </CButton>
                          </CCol>
                        </CRow>
                      </Form>
                    )}
                  </Formik>
                </CCardBody>
              </CCard>
            </CCardGroup>
          </CCol>
        </CRow>
      </CContainer>
    </div>
  );
};
export default Login;
