import React, { useState } from "react";
import { CMultiSelect } from "@coreui/react-pro";
import { connect, Field, ErrorMessage, getIn, useField } from "formik";
import TextError from "./TextError";

const Multiselect = (props) => {
  let { formik, name, id, options, ...rest } = props;
  const { handleChange, handleBlur } = formik;
  const error = getIn(formik.errors, name);
  const touch = getIn(formik.touched, name);
  const [selected, setSelected] = useState([]);
  let final = [];
  return (
    <>
      <Field name={name} id={id} onChange={handleChange} onBlur={handleBlur}>
        {({ field }) => {
          selected.map((d) => {
            final.push(d.value);
            return final;
          });
          formik.values[`${name}`] = final;

          return (
            <CMultiSelect
              {...field}
              {...rest}
              options={options}
              onChange={setSelected}
              className={`form-control  ${error && touch ? "is-invalid" : ""}`}
            />
          );
        }}
      </Field>
      <ErrorMessage name={name} component={TextError} />
    </>
  );
};

export default connect(Multiselect);
